<template>
  <div>
    <div class="flex-between">
      <h3>差评过多商家</h3>
      <div>
        <span>差评数：</span>
        <el-input style="width:150px" v-model="num" type="number" min="0"></el-input>
      </div>
    </div>
    <el-table :data="shopperlist" height="410" border>
      <el-table-column prop="u_id" label="商家ID" />
      <el-table-column prop="u_nickname" label="商家名" />
      <el-table-column prop="u_tel" label="联系电话" />
      <el-table-column prop="u_email" label="邮箱" />
      <el-table-column label="操作">
        <div slot-scope="scope">
          <el-button title="添加服务" type="primary" size="mini" icon="iconfont icon-jiahao" @click="addserver(scope.row)" />
          <el-button title="修改" type="warning" size="mini" icon="iconfont icon-shenqing" @click="alert(scope.row)" />
        </div>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  data() {
    return {
      num: 0,
      shopperlist: []
    }
  },
  created() {
    this.getshopperlist()
  },
  methods: {
    async getshopperlist() {
      const { data: res } = await this.$http.get("showBadGoods", { params: { num: this.num } })
      if (res.code !== 200) return
      this.shopperlist = res.obj
    }
  },
  watch: {
    num(val) {
      this.getshopperlist()
    }
  }
}
</script>
